  })\n            .then ( response => {\n                if (response.status == 200 ) {\n                    this.$emit('todochanged');\n                }\n            })\n            .catch ( error => {\n                console.log(error);\n            })\n        }\n    },\n}\n</script>\n<style scoped>\n    .done {\n        text-decoration: line-through;\n        color: #999999;\n    }\n\n</style>"],"sourceRoot":""}]);
